/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  font-family: "PT Serif", serif;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  height: 100%;
  position: relative;
  display: flex;
}

.module .container {
  height: 100%;
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  transition: margin-left 250ms ease-out, transform 250ms ease-out;
  width: 245px;
  color: white;
  position: relative;
  color: #ffffff;
  justify-content: space-between;
}

.module .body {
  min-height: 80px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.module .loading {
  margin-top: 100px;
}

.module .header {
  margin: 12px;
  position: relative;
  display: block;
}

.module .element {
  width: 219px;
  display: flex;
  flex-direction: column;
  gap: 7px;
  margin: 0 12px;
}

.module .projects {
  background: inherit;
}

.module .menu {
  display: flex;
  justify-content: space-between;
}

.module .logo {
  object-fit: contain;
  height: 24px;
  width: auto;
}

.module .menuHamburguer {
  object-fit: contain;
  height: 13px;
  width: auto;
  cursor: pointer;
}

.module .flexColumnElement {
  display: flex;
  flex-direction: column;
  gap: 12px;
  height: 100%;
}

.module .select {
  width: 219px;
}
.module .select * {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 13px !important;
}

.module button {
  font-family: inherit;
  border: none;
  border-radius: 3px;
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  width: 219px;
}

.module .navButton {
  display: block;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: inherit;
}
.module .navButton.active .button {
  background-color: #198ce5;
  color: #f7f7f7;
}

.module .button {
  color: #cbcbcb;
  border-radius: 3px;
  background-color: #6c757d;
  text-align: left;
  padding: 7px 10px 7px 16px;
}
.module .button:hover {
  background-color: #198ce5;
  color: #f7f7f7;
}

.module .sectionTitle {
  font-size: 11px;
  color: #e6f5fc;
  text-transform: uppercase;
}

