/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  height: 100%;
  width: 100%;
}

.background {
  position: absolute;
  z-index: -100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #454545;
}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

.module {
  display: block;
  padding: 12px;
  cursor: pointer;
}

.title {
  display: flex;
  font-weight: normal !important;
  margin-bottom: 8px;
}
.title i {
  color: #a0a0a0;
}
.title a {
  color: #777;
  font-family: inherit;
}
.title .numH {
  color: #F0B010;
  font-size: 0.85em;
  margin-left: 4px;
}

.options {
  position: absolute;
  top: 10px;
  background: #fff;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.module:hover .options {
  text-decoration: none;
  opacity: 1;
}

.options .option {
  display: inline-block;
  margin: 2px;
  font-size: 13px;
  color: white;
  padding: 4px 5px;
  height: 23px;
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
}

.add {
  background-color: #198CE5;
}

.info {
  background-color: #FFB500;
}

.edit {
  background-color: #f0a000;
}

.bot {
  background-color: #FF5F00;
}

.insight {
  background-color: #20b068;
}

.add:hover {
  background-color: #1077D5;
}

.info:hover {
  background-color: #FFA000;
}

.edit:hover {
  background-color: #f08500;
}

.bot:hover {
  background-color: #f08500;
}

.insight:hover {
  background-color: #10a060;
}

.options a {
  text-decoration: none;
}
.options a:hover {
  text-decoration: none;
}

.container {
  position: relative;
  width: 100%;
}

.container.other {
  display: flex;
  flex-wrap: wrap;
}

.wrapperOther {
  position: relative;
  width: 25%;
  padding: 0px 10px 0px 10px;
}

.item {
  padding: 6px 0;
}

