/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  width: 100%;
  height: 100%;
}

.header {
  position: relative;
  width: 100%;
  padding: 23px 20px 28px 20px;
  color: #fff;
  font-size: 19px;
}

.headerTitle {
  font-size: 19px;
  cursor: pointer;
}

.header .dropdown {
  display: inline-block;
  margin-left: 5px;
  font-size: 19px;
  padding: 4px;
}

.headerBackground {
  position: absolute;
  width: 100%;
  top: 0px;
  left: 0px;
  z-index: 0;
  height: 200px;
  -webkit-border-radius: 5px 5px 0 0;
  -moz-border-radius: 5px 5px 0 0;
  -ms-border-radius: 5px 5px 0 0;
  border-radius: 5px 5px 0 0;
}

.headerBackground.insights {
  background-color: #1cab68;
}

.headerBackground.deepdive {
  background-color: #29244e;
}

.headerBackground.comments {
  background-color: #279af3;
}

.headerBackground.help {
  background-color: #ffb500;
}

.menu {
  position: absolute;
  right: 10px;
  top: 50px;
}

.menu > * {
  display: inline-block;
}

.menuItem {
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  padding: 5px 12px;
}

.navItem.active .menuItem {
  color: #222;
  background: #fff;
  -webkit-border-radius: 5px 5px 0 0;
  -moz-border-radius: 5px 5px 0 0;
  -ms-border-radius: 5px 5px 0 0;
  border-radius: 5px 5px 0 0;
}

.canvas {
  position: relative;
  background: #fff;
  width: 40%;
  height: 430px;
  overflow: auto;
  border-right: 1px solid #e7e7e7;
  -webkit-border-radius: 0px 0px 0px 5px;
  -moz-border-radius: 0px 0px 0px 5px;
  -ms-border-radius: 0px 0px 0px 5px;
  border-radius: 0px 0px 0px 5px;
}

.content {
  position: relative;
  padding: 20px 20px 30px 20px;
  background: #f5f5f5;
  width: 60%;
  left: 40%;
  margin-top: -430px;
  height: 430px;
  -webkit-border-radius: 0px 0px 5px 0px;
  -moz-border-radius: 0px 0px 5px 0px;
  -ms-border-radius: 0px 0px 5px 0px;
  border-radius: 0px 0px 5px 0px;
}

