@use "src/resources/base" as base;
@use "src/resources/forms" as forms;
@use "src/resources/layout" as layout;

.loading {
  width: 100%;
  height: 100%;
}

.loading > div {
  position: relative;
  z-index: 10;
  width: 200px;
  height: 200px;
  top: 50%;
  left: 50%;
  margin-top: -110px;
  margin-left: -50px
}

.header {
  position: relative;
  width: 100%;
  padding: 10px 15px;
  font-size: 14px;
  display: flex;
  justify-content: flex-end;
  gap: 14px;
  color: #555;
}

.menu {
  display: inline;
  background-color: #fbfbfb;
  border: 1px solid #e0e0e0;
  padding: 7px 10px;

  &.options {
    background: #fff;
    cursor: pointer;
  }
  span {
    color: #333;
  }
  
  .weeks,
  .filters {
    display: inline;
  }

  .weeks {
  
    select {
      width: 5ch;
      background: #fafafa;
      border: 1px solid #ddd;
    }
    
    span {
      margin-right: 5px;
    }
  }

  .filters {
    display: flex;
    align-items: center;
    gap: 12px;
    
    label {
      font-size: 14px;
      color: #999;
    }
    div{
      display: flex;
      align-items: center;
      gap: 2px;
    }
  }
}

.panel {
  position: relative;
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
}