/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  width: 100%;
  height: 100%;
}

.header {
  width: 100%;
  padding: 23px 20px;
  background-color: #198CE5;
  color: #fff;
  font-size: 20px;
  -webkit-border-radius: 5px 5px 0 0;
  -moz-border-radius: 5px 5px 0 0;
  -ms-border-radius: 5px 5px 0 0;
  border-radius: 5px 5px 0 0;
}

.content {
  padding: 20px 20px 30px 20px;
  background: #fff;
  -webkit-border-radius: 0px 0px 5px 5px;
  -moz-border-radius: 0px 0px 5px 5px;
  -ms-border-radius: 0px 0px 5px 5px;
  border-radius: 0px 0px 5px 5px;
}
.content form {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.hypothesis {
  width: 100%;
  padding: 8px 14px;
  font-size: 16px;
  min-height: 64px;
  max-height: 128px;
  border: 1px solid #e0e0e0;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  border-radius: 6px;
}

.customers b,
.colors b,
.types b {
  display: inline-block;
  margin-bottom: 7px;
}

.typeContainer {
  border: 1px solid #e0e0e0;
  padding: 8px 16px;
  height: 112px;
  overflow: auto;
}

.call {
  position: relative;
  width: 100%;
  display: inline-block;
  padding: 9px 14px;
  text-align: center;
  font-size: 15px;
  color: #fff;
  background-color: #11C26F;
  border: none;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  border-radius: 6px;
}

.error {
  width: 100%;
  margin-top: -10px;
  margin-bottom: 10px;
  background-color: #ffeeee;
  color: #a05050;
  font-size: 14px;
  padding: 12px;
  font-weight: bold;
}

.call:hover {
  background-color: #11B24F;
}

