/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  height: 100%;
  width: 100%;
}

.background {
  position: absolute;
  z-index: -100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #454545;
}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

.module {
  overflow: auto;
}
.module p {
  margin: 0;
}

.feedBody {
  color: #343434;
  background-color: #F5F5F5;
  padding: 60px 70px 60px 50px;
  min-height: calc(100vh - 56px);
}
.feedBody .title {
  font-size: 18px;
  margin-bottom: 16px;
}
.feedBody .feeds .feed {
  display: flex;
  margin-bottom: 25px;
}
.feedBody .feeds .feed:last-child {
  margin-bottom: 0px;
}
.feedBody .feeds .feed .feedLeft {
  margin-right: 25px;
}
.feedBody .feeds .feed .feedLeft .ring {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 4px solid #A2A2A2;
}
.feedBody .feeds .feed .feedLeft .ring.red {
  border: 4px solid #F96276;
}
.feedBody .feeds .feed .feedLeft .ring.purple {
  border: 4px solid #5B558B !important;
}
.feedBody .feeds .feed .feedLeft .ring.green {
  border: 4px solid #11C26F !important;
}
.feedBody .feeds .feed .feedTop {
  display: flex;
  align-items: center;
  margin-bottom: 14px;
}
.feedBody .feeds .feed .feedTop .feedTitle {
  margin-right: 25px;
}
.feedBody .feeds .feed .feedTop .date {
  color: #686868;
}
.feedBody .feeds .feed .feedText {
  background-color: white;
  padding: 15px 18px;
  width: 100%;
}
.feedBody .feeds .feed .feedText .feedTextInfo {
  color: #686868;
}

