/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module .buttonLink {
  background: none;
  border: none;
  padding: 0 !important;
  font-weight: bold;
  color: #413c6b;
  cursor: pointer;
  box-sizing: border-box;
}
.module .buttonLink:hover {
  box-sizing: border-box;
  text-decoration: underline;
}

.module .buttonGreen {
  color: #ffffff;
  background-color: #11c26f;
}
.module .buttonGreen:hover {
  background-color: #0fa063;
}

.module .mainButton {
  border-radius: 7px;
  width: 100%;
}

.module .input, .module .select {
  background-color: #f5f5f5;
  border: solid 1px #cbcbcb;
  padding: 8px 12px;
}

.module .button {
  background-color: #413c6b;
  color: #ffffff;
  height: 36px;
  border-radius: 18px;
}
.module .button.lg {
  height: 48px;
  border-radius: 24px;
}

.module .select {
  height: 48px;
}

.module {
  height: 100%;
  width: 100%;
}

.background {
  position: absolute;
  z-index: -100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #454545;
}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

.loading {
  width: 100%;
  height: 100%;
}

.loading > div {
  position: relative;
  z-index: 10;
  width: 200px;
  height: 200px;
  top: 50%;
  left: 50%;
  margin-top: -110px;
  margin-left: -50px;
}

.header {
  position: relative;
  width: 100%;
  padding: 10px 15px;
  font-size: 14px;
  display: flex;
  justify-content: flex-end;
  gap: 14px;
  color: #555;
}

.menu {
  display: inline;
  background-color: #fbfbfb;
  border: 1px solid #e0e0e0;
  padding: 7px 10px;
}
.menu.options {
  background: #fff;
  cursor: pointer;
}
.menu span {
  color: #333;
}
.menu .weeks,
.menu .filters {
  display: inline;
}
.menu .weeks select {
  width: 5ch;
  background: #fafafa;
  border: 1px solid #ddd;
}
.menu .weeks span {
  margin-right: 5px;
}
.menu .filters {
  display: flex;
  align-items: center;
  gap: 12px;
}
.menu .filters label {
  font-size: 14px;
  color: #999;
}
.menu .filters div {
  display: flex;
  align-items: center;
  gap: 2px;
}

.panel {
  position: relative;
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
}

