/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  position: relative;
  display: flex;
}

.module .element {
  width: 219px;
  display: flex;
  flex-direction: column;
  gap: 7px;
  margin: 0 12px;
}

.module button {
  font-family: inherit;
  border: none;
  border-radius: 3px;
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  width: 219px;
}

.module .navButton {
  display: block;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: inherit;
}
.module .navButton.active .button {
  background-color: #198ce5;
  color: #f7f7f7;
}

.module .button {
  color: #ffffff;
  border-radius: 3px;
  background-color: #6c757d;
  text-align: left;
  padding: 7px 10px 7px 16px;
}
.module .button:hover {
  background-color: #198ce5;
  color: #f7f7f7;
}

.module .buttonOption {
  float: right;
  width: 30px;
  padding: 2px 0px;
  font-size: 13px;
  text-align: right;
  z-index: 2;
}

.module .sectionTitle {
  font-size: 11px;
  color: #e6f5fc;
  text-transform: uppercase;
}

