/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  width: 100%;
  height: 100%;
}
.module p {
  margin: 0;
}

.title {
  position: relative;
  font-size: 23px;
  top: 35px;
  left: 20px;
  font-family: "PT Serif", serif;
}

.projectSub {
  font-size: 14px;
  margin-left: 12px;
}

.project {
  background-color: white;
  margin-bottom: 30px;
  padding: 20px 0px;
}
.project:last-child {
  margin-bottom: 0px;
}
.project .projectName {
  font-size: 24px;
  margin-bottom: 25px;
}
.project .buttonOption {
  display: inline-block;
  float: right;
  margin-top: -40px;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.project .member:first-child {
  border-top: 1px solid #dbdbdb;
}
.project .member {
  display: flex;
  align-items: center;
  margin-left: 10px;
  border-bottom: 1px solid #dbdbdb;
  padding: 10px 0;
}
.project .member .profilePic {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 30px;
  vertical-align: middle;
  background-clip: padding-box;
  background-size: cover;
  background-position: center center;
}
.project .member .memberInfo {
  margin-top: 7px;
  margin-right: 30px;
}
.project .member .memberInfo .name {
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 0px;
}
.project .member .memberInfo .mail {
  font-size: 14px;
  font-weight: 400;
}
.project .member .deleteButton {
  flex-grow: 1;
  text-align: right;
}
.project .member .deleteButton button {
  border: none;
  background-color: none;
}
.project .member .deleteButton svg {
  color: #686868;
}
.project .member .deleteButton:hover svg {
  color: #343434;
}

.addMemberBtn {
  color: #198CE5;
  border: 1px solid #198CE5;
  border-radius: 24px;
  padding: 6px 15px;
  margin-top: 20px;
  margin-bottom: 24px;
  display: inline-block;
}

