.module .buttonLink {
  background: none;
  border: none;
  padding: 0 !important;
  font-weight: bold;
  color: #413c6b;
  cursor: pointer;
  box-sizing: border-box;
}
.module .buttonLink:hover {
  box-sizing: border-box;
  text-decoration: underline;
}

.module .buttonGreen {
  color: #ffffff;
  background-color: #11c26f;
}
.module .buttonGreen:hover {
  background-color: #0fa063;
}

.module .mainButton {
  border-radius: 7px;
  width: 100%;
}

.module .input, .module .select {
  background-color: #f5f5f5;
  border: solid 1px #cbcbcb;
  padding: 8px 12px;
}

.module .button {
  background-color: #413c6b;
  color: #ffffff;
  height: 36px;
  border-radius: 18px;
}
.module .button.lg {
  height: 48px;
  border-radius: 24px;
}

.module .select {
  height: 48px;
}

/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  height: 100%;
  width: 100%;
}

.background {
  position: absolute;
  z-index: -100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #454545;
}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

.module {
  font-family: "PT Serif", serif;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  height: 100%;
  width: 100%;
  font-family: "PT Serif", serif;
  background: #fff;
  overflow: auto;
  padding: 35px 0px;
}

.module label {
  font-family: "PT Serif", serif;
  font-size: 14px;
}

.module .selectAllBtn {
  padding: 9px 10px;
  font-size: 15px;
  border: 1px solid rgba(0, 0, 0, 0.125);
  cursor: pointer;
}
.module .selectAllBtn input {
  margin-right: 10px;
}

.module .featureTitle {
  margin-top: 12px;
  margin-bottom: 8px;
  font-family: "PT Serif", serif;
  font-style: italic;
}

.module .question {
  font-style: italic;
}

.module input,
.module select,
.module textarea {
  font-family: "PT Serif", serif;
}

.module h4 {
  margin-bottom: 30px;
}

.module hr {
  margin-top: 30px;
  margin-bottom: 20px;
}

.module .selected {
  background-color: #413c6b;
}

.module .breadcrumb {
  font-family: "PT Serif", serif;
  font-size: 18px;
  font-weight: bold;
  color: #343434;
}
.module .breadcrumb.breadcrumbLight {
  color: #6c757d;
}

.module .subtitle {
  font-family: "PT Serif", serif;
  font-size: 18px;
  color: grey-dark;
}

.module button {
  background-color: #11c26f;
  color: #ffffff;
  min-height: 43px !important;
  border-radius: 24px;
  font-size: 15px;
}
.module button:hover {
  color: #ffffff;
  background-color: #0fa063;
}

