/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-auto-rows: 30px;
  gap: 6px;
}

.item {
  color: #fff;
  cursor: pointer;
  align-content: center;
  width: 100%;
  text-align: center;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  border-radius: 6px;
}

.tick {
  display: inline-block;
}

