/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  height: 100%;
  width: 100%;
}

.background {
  position: absolute;
  z-index: -100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #454545;
}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

.fullwidth {
  max-width: 100%;
  margin: 0;
}

.container {
  min-height: 100%;
}

.row,
.innercol {
  min-height: 1px;
}

.innerrow {
  min-height: 1px;
  margin: 0px;
}

.col {
  padding: 0px;
}

.col.header {
  padding: 8px 0px 6px 0px;
  text-align: center;
}

.col.header b {
  font-size: 13px;
}

.content {
  position: relative;
  border: 1px solid #e7e7e7;
  background: #fff;
  width: 100%;
  height: 100%;
}

.content.other {
  margin-top: 20px;
}

.footer {
  background-color: #FFBA13;
  visibility: hidden;
  max-width: 100%;
  margin: 12px 0px 0px 0px;
  padding: 4px;
  font-size: 13px;
  color: #fff;
  text-align: center;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  border-radius: 4px;
}

.brand {
  background-color: #343434;
  color: #fff;
  max-width: 100%;
  margin-top: 5px;
  margin-bottom: 3px;
  padding: 4px;
  font-size: 13px;
  text-align: center;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  border-radius: 4px;
}

