/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.module .element {
  width: 219px;
  display: flex;
  flex-direction: column;
  gap: 7px;
  margin: 0 12px;
}

.module .team {
  width: 219px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.module .teamSection {
  display: flex;
  align-items: center;
  gap: 6px;
}

.module .teamElementDefault,
.teamElementMore {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
  background-color: rgba(255, 255, 255, 0.03);
  cursor: pointer;
}

.teamElementMore {
  width: 23px;
  height: 23px;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.5);
}

.module .teamElement {
  cursor: pointer;
}

.module .teamImage {
  display: inline-block;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  object-fit: cover;
  overflow: hidden;
}

.module .buttonAdd {
  color: #e6f5fc;
}

.module .buttonAddIcon {
  font-size: 19px;
  font-weight: bold;
}

.module .buttonAdd:hover {
  color: #f7f7f7;
}

.module .sectionTitle {
  font-size: 11px;
  color: #e6f5fc;
  text-transform: uppercase;
}

