/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  height: 100%;
  width: 100%;
}

.background {
  position: absolute;
  z-index: -100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #454545;
}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

.module .buttonLink {
  background: none;
  border: none;
  padding: 0 !important;
  font-weight: bold;
  color: #413c6b;
  cursor: pointer;
  box-sizing: border-box;
}
.module .buttonLink:hover {
  box-sizing: border-box;
  text-decoration: underline;
}

.module .buttonGreen {
  color: #ffffff;
  background-color: #11c26f;
}
.module .buttonGreen:hover {
  background-color: #0fa063;
}

.module .mainButton {
  border-radius: 7px;
  width: 100%;
}

.module .input, .module .select {
  background-color: #f5f5f5;
  border: solid 1px #cbcbcb;
  padding: 8px 12px;
}

.module .button {
  background-color: #413c6b;
  color: #ffffff;
  height: 36px;
  border-radius: 18px;
}
.module .button.lg {
  height: 48px;
  border-radius: 24px;
}

.module .select {
  height: 48px;
}

.module > div > div {
  margin-top: 0px !important;
}

.financial_button {
  position: relative;
  display: inline-block;
  padding: 9px 28px;
  text-align: center;
  margin-top: 18px !important;
  margin-bottom: 12px;
  margin-left: 40px;
  font-size: 14px;
  color: #fff;
  background-color: #11c26f;
  border: none;
  cursor: pointer;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  border-radius: 20px;
}

@media (max-width: 1200px) {
  .financial_button {
    margin-left: 20px;
  }
}

