.chatbotContainer {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
}

.chatbotWindow {
  position: fixed;
  bottom: 6rem;
  right: 20px;
  width: min(540px, 100vw - 40px);
  height: min(600px, 80vh);
  background-color: #ffffff;
  box-shadow: 0px 0.4rem 1.2rem rgba(0, 0, 0, 0.1);
  border-radius: 0.8rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.chatbotWindow .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FF5F00;
  color: white;
  padding: 0.5rem 1rem;
}
.chatbotWindow .header .closeButton {
  background: none;
  border: none;
  color: white;
  font-size: 2rem;
  padding: 0;
}

.chatButton {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #FF5F00;
  color: white;
  border-radius: 50%;
  border: none;
  width: 56px;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
  font-size: 28px;
}

.messageArea {
  flex: 1;
  padding: 1rem;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.messageBlock {
  font-size: 14px;
  word-wrap: break-word;
  white-space: pre-line;
  max-width: 75%;
}

.chatbotInputArea {
  display: flex;
  align-items: center;
  border-top: 0.1rem solid #e0e0e0;
  padding: 1rem;
  background-color: #fafafa;
}
.chatbotInputArea .textarea {
  padding: 8px 12px;
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #ccc;
  margin-right: 10px;
  width: 100%;
  white-space: normal;
  resize: none;
}
.chatbotInputArea button {
  background-color: #FF5F00;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
  cursor: pointer;
  font-size: 1rem;
}

