/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module .buttonLink {
  background: none;
  border: none;
  padding: 0 !important;
  font-weight: bold;
  color: #413c6b;
  cursor: pointer;
  box-sizing: border-box;
}
.module .buttonLink:hover {
  box-sizing: border-box;
  text-decoration: underline;
}

.module .buttonGreen {
  color: #ffffff;
  background-color: #11c26f;
}
.module .buttonGreen:hover {
  background-color: #0fa063;
}

.module .mainButton {
  border-radius: 7px;
  width: 100%;
}

.module .input, .module .select {
  background-color: #f5f5f5;
  border: solid 1px #cbcbcb;
  padding: 8px 12px;
}

.module .button {
  background-color: #413c6b;
  color: #ffffff;
  height: 36px;
  border-radius: 18px;
}
.module .button.lg {
  height: 48px;
  border-radius: 24px;
}

.module .select {
  height: 48px;
}

.module {
  height: 100%;
  width: 100%;
}

.background {
  position: absolute;
  z-index: -100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #454545;
}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

.module {
  position: relative;
}

.colors {
  width: 100%;
  margin-top: -4px;
}

.colors > * {
  display: inline-block;
}

.color {
  width: 19px;
  height: 6px;
  margin-right: 3px;
  cursor: pointer;
}

.content {
  width: 100%;
  color: #222;
  font-size: 14px;
  cursor: pointer;
}

.content.valid {
  text-decoration: underline;
  text-decoration-color: #40a040;
}

.content.invalid {
  text-decoration: line-through;
  text-decoration-color: #FF8050;
}

.options {
  position: absolute;
  background: #f5f5f5;
  border: 1px solid #e0e0e0;
  bottom: 50%;
  transform: translateY(50%);
  right: 10px;
  display: none;
  cursor: pointer;
  -webkit-border-radius: 14px;
  -moz-border-radius: 14px;
  -ms-border-radius: 14px;
  border-radius: 14px;
}

.options.focused {
  border-image-source: none;
}

.options > * {
  display: inline-block;
}

.options .optionItem {
  display: inline-block;
  padding: 0px;
  margin: 2px;
  font-size: 16px;
  padding: 4px 5px;
}

.validate {
  color: #40a040;
}

.invalidate {
  color: #FF8050;
}

.edit {
  color: #198CE5;
}

.delete {
  color: #555;
}

.footer {
  width: 100%;
}

.tags {
  padding-top: 3px;
  padding-right: 32px;
}

.tags > * {
  display: inline-block;
}

.tag {
  width: 20px;
  height: 20px;
  text-align: center;
  font-size: 12px;
  color: #444;
  border: 1px solid #e0e0e0;
  background: #eee;
  margin-right: 5px;
  cursor: pointer;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
}

