.module .buttonLink {
  background: none;
  border: none;
  padding: 0 !important;
  font-weight: bold;
  color: #413c6b;
  cursor: pointer;
  box-sizing: border-box;
}
.module .buttonLink:hover {
  box-sizing: border-box;
  text-decoration: underline;
}

.module .buttonGreen {
  color: #ffffff;
  background-color: #11c26f;
}
.module .buttonGreen:hover {
  background-color: #0fa063;
}

.module .mainButton {
  border-radius: 7px;
  width: 100%;
}

.module .input, .module .select {
  background-color: #f5f5f5;
  border: solid 1px #cbcbcb;
  padding: 8px 12px;
}

.module .button {
  background-color: #413c6b;
  color: #ffffff;
  height: 36px;
  border-radius: 18px;
}
.module .button.lg {
  height: 48px;
  border-radius: 24px;
}

.module .select {
  height: 48px;
}

/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module {
  height: 100%;
  width: 100%;
}

.background {
  position: absolute;
  z-index: -100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #454545;
}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

.module {
  font-family: "PT Serif", serif;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  height: 100%;
  width: 100%;
}

.module select {
  text-transform: capitalize;
}

.module .badgeEdit {
  font-size: 12px;
  padding: 8px 0px;
  color: #ffffff;
  background-color: #44a8f5;
  padding: 8px;
  border-radius: 4px;
  margin-right: 8px;
}

.module .blueGradient {
  background: #FF5F00;
  color: #ffffff;
}

.module .dataList .ul {
  list-style-type: none;
}

.module .spacing {
  margin-top: 24px;
}

.module .spacing p {
  color: rgba(255, 255, 255, 0.9);
  font-family: "Open Sans", sans-serif;
}

.module .cardTitle {
  font-size: 19px;
  line-height: 1.4em;
  font-weight: bolder;
}

.module .keyPerformanceTable {
  border: none;
  width: 100%;
  padding: 32px;
  border-radius: 4px;
}
.module .keyPerformanceTable .secondary {
  color: #6c757d;
  font-size: 9px;
}
.module .keyPerformanceTable .icon {
  color: #413c6b;
}
.module .keyPerformanceTable .subtitle {
  font-size: 16px;
  font-weight: bold;
}
.module .keyPerformanceTable th {
  font-weight: bold;
  text-align: center;
}
.module .keyPerformanceTable th,
.module .keyPerformanceTable td {
  text-align: center;
  padding-top: 8px;
  width: 10%;
}

.module .light {
  color: #ffffff;
}

.module .headerForm label {
  margin-top: 16px;
  margin-bottom: 2px;
}

.module .fancyNumber, .module .keyPerformanceTable .featuresNumbers td {
  font-family: sans-serif;
  font-size: 24px;
  font-weight: bolder;
  color: #343434;
}

.module .flexRowContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.module .marketData .fancyNumber, .module .marketData .keyPerformanceTable .featuresNumbers td, .module .keyPerformanceTable .featuresNumbers .marketData td {
  padding-top: 4px;
  padding-bottom: 16px;
}

.module .marketChart {
  margin-top: -40px;
  padding-bottom: 68%;
  width: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
@media screen and (max-width: 800px) {
  .module .marketChart {
    padding-bottom: 80%;
  }
}
.module .marketChart .chartElement, .module .marketChart .target, .module .marketChart .server, .module .marketChart .total {
  border-radius: 100%;
  transform: 40deg;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  position: absolute;
}
.module .marketChart .total {
  top: 0;
  bottom: auto;
  -moz-transform: rotate(40deg);
  -ms-transform: rotate(40deg);
  -webkit-transform: rotate(40deg);
  transform: rotate(40deg);
  padding-bottom: 70%;
  width: 70%;
  background: #1cab68;
  z-index: 200;
}
.module .marketChart .server {
  background: #50a0ff;
  border: 2px solid rgba(255, 255, 255, 0.7);
}
.module .marketChart .target {
  z-index: 202;
  background: #ffc040;
  border: 2px solid rgba(255, 255, 255, 0.7);
}

.module .alignRight {
  text-align: right;
}

.module .irlTable {
  width: 100%;
}
.module .irlTable td {
  padding-bottom: 7px;
  padding-right: 7px;
  color: #707070;
}

.module ul.details {
  font-family: "Open Sans", sans-serif;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.module ul.details li {
  float: left;
  margin-right: 12px;
}

.module ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.module ul .bulletTitle {
  font-weight: bold;
}

.module .trlSteperElement {
  display: block;
  border: 1px solid #11c26f;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  line-height: 24px;
}

.module .trlSteperCompleted {
  color: #ffffff;
}

.module .trlContainer {
  background: none;
}

.module .trlSuccess,
.module .trlPending {
  border-radius: 25px;
  margin-left: 2px;
  height: 24px;
}

.module .trlSuccess {
  background-color: #11c26f;
}

.module .trlPending {
  background-color: #f0f0f0;
  color: #777;
}

.module .projectInfoList li {
  margin-bottom: 4px;
}
.module .projectInfoList a {
  color: #fff;
}

.module .editGeneric, .module .editLight, .module .edit {
  border: none;
  padding: none;
  background: none;
  font-size: 23px;
}

.module .edit {
  color: #343434;
  margin-left: 8px;
}
.module .edit i {
  font-size: 15px;
}
.module .edit:hover {
  color: #44a8f5;
}

.module .editLight {
  border: none;
  padding: none;
  margin: none;
  color: #ffffff;
  margin-left: 8px;
}
.module .editLight:hover {
  color: #feff7c;
}

.module .keyFeaturesTableContainer {
  padding: 10px 0px;
}

.module .keyFeaturesTable td {
  padding: 14px;
}
.module .keyFeaturesTable .keyFeatureNumber {
  background-color: #11c26f;
  color: #ffffff;
}
.module .financialSwitch {
  float: right;
  margin-top: -5px;
}

.module .openButton {
  background-color: #11c26f;
  color: #ffffff;
  min-height: 32px !important;
  width: 120px;
  margin-left: 10px;
  border-radius: 24px;
  font-size: 15px;
  margin-top: -4px;
}
.module .openButton:hover {
  color: #ffffff;
  background-color: #0fa063;
}

.module .horizonBox {
  position: relative;
  border: 1px solid #a0a0a0;
  margin-left: 35px;
}

.module .horizonText {
  position: relative;
  width: 80px;
  height: 20px;
  margin-top: -20px;
  font-size: 13px;
  text-align: center;
}

