/*
__Seed builder__v0.2.0
  (Read_only) Builder helper
*/
.module .buttonLink {
  background: none;
  border: none;
  padding: 0 !important;
  font-weight: bold;
  color: #413c6b;
  cursor: pointer;
  box-sizing: border-box;
}
.module .buttonLink:hover {
  box-sizing: border-box;
  text-decoration: underline;
}

.module .buttonGreen {
  color: #ffffff;
  background-color: #11c26f;
}
.module .buttonGreen:hover {
  background-color: #0fa063;
}

.module .mainButton {
  border-radius: 7px;
  width: 100%;
}

.module .input, .module .select {
  background-color: #f5f5f5;
  border: solid 1px #cbcbcb;
  padding: 8px 12px;
}

.module .button {
  background-color: #413c6b;
  color: #ffffff;
  height: 36px;
  border-radius: 18px;
}
.module .button.lg {
  height: 48px;
  border-radius: 24px;
}

.module .select {
  height: 48px;
}

.module {
  height: 100%;
  width: 100%;
}

.background {
  position: absolute;
  z-index: -100;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;
  background-color: #454545;
}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

.background {
  position: fixed;
  z-index: 0;
}

.container {
  height: inherit;
}

.title {
  margin-bottom: 36px;
}

.logoCanou {
  display: inline-block;
  height: 40px;
  width: auto;
  margin-top: 30px;
  margin-left: 40px;
}

.containerModifier {
  height: 100%;
  position: relative;
}

.rowCompleteHeight {
  width: 100%;
  margin: 0px;
}
.rowCompleteHeight .columnCenter {
  height: 100%;
  display: flex;
  max-width: 640px;
  flex-direction: column;
  justify-content: center;
}
.rowCompleteHeight .columnCenter .card {
  width: inherit;
  margin: 50px 0 20px;
  padding: 40px 50px;
}

.txtLabel {
  margin-bottom: 5px;
  font-size: 14px;
  color: #666;
}

.txtField {
  background-color: #f5f5f5;
  margin-bottom: 15px;
  padding: 11px 16px;
  border: 1px solid #e7e7e7;
  -webkit-border-radius: 7px;
  -moz-border-radius: 7px;
  -ms-border-radius: 7px;
  border-radius: 7px;
}
.txtField:hover, .txtField:focus {
  background-color: #cbcbcb;
}

.checkboxContainer {
  display: flex;
  color: #444;
  flex-direction: row;
}

.checkboxItem {
  white-space: nowrap;
}

.optionsContainer {
  margin-top: 20px;
  margin-bottom: 10px;
  color: #444;
}

.signin {
  margin-bottom: 5px;
  white-space: nowrap;
}

.error {
  background-color: #ffeeee;
  color: #a05050;
  width: 100%;
  margin-bottom: 10px;
  font-size: 14px;
  padding: 12px;
  font-weight: bold;
}

.payment {
  background-color: #f0eef0;
  color: #a050a0;
  text-align: center;
  width: 100%;
  margin-bottom: 10px;
  font-size: 14px;
  padding: 12px;
  font-weight: bold;
}

.loginButton {
  height: 45px;
  font-size: 16px;
}

.footer {
  color: #cbcbcb;
}

.footer a {
  color: #cbcbcb;
}

