.module .buttonLink {
  background: none;
  border: none;
  padding: 0 !important;
  font-weight: bold;
  color: #413c6b;
  cursor: pointer;
  box-sizing: border-box;
}
.module .buttonLink:hover {
  box-sizing: border-box;
  text-decoration: underline;
}

.module .buttonGreen {
  color: #ffffff;
  background-color: #11c26f;
}
.module .buttonGreen:hover {
  background-color: #0fa063;
}

.module .mainButton {
  border-radius: 7px;
  width: 100%;
}

.module .input, .module .select {
  background-color: #f5f5f5;
  border: solid 1px #cbcbcb;
  padding: 8px 12px;
}

.module .button {
  background-color: #413c6b;
  color: #ffffff;
  height: 36px;
  border-radius: 18px;
}
.module .button.lg {
  height: 48px;
  border-radius: 24px;
}

.module .select {
  height: 48px;
}

