}

.module .spacingContainer {
  padding-top: 20px;
}

.module .spacingText {
  padding-top: 4px;
}

.module .card {
  border-radius: 4px;
  border: 1px solid #cbcbcb;
}
.module .card .main {
  color: #ffffff;
  border: none;
  padding: 12px;
}
.module .card .main .mainCardText {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
}
.module .card .mainCardTitle {
  font-size: 38px;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  font-weight: bolder;
}

.module .title {
  font-size: 32px;
  font-weight: bold;
}
.module .title.titleWhite {
  color: #ffffff;
}

.module .completeWidth {
  width: 100%;
}

.module a.link {
  cursor: pointer;
  color: #198ce5;
}
.module a.link:hover {
  text-decoration: underline;
}

.module .tableRight td {
  text-align: right;
}

